*{
  box-sizing: border-box;
}
body{
  background: #d36060;
  margin: 0;
}
h2{
  background: #d36060;
  padding: 25px;
  color: #FFF;
  text-align: center;
  letter-spacing: 2px;
}
.App{
  padding: 30px;
  background: #FFF;
  width: 80%;
  margin: 50px auto;
  box-shadow: 1px 2px 5px #eee;
}
.add{
  background:#6a6a6a;}

input{
  width: 80%;
  border: 0;
  padding: 9px 10px;
  background: #e8e8e8;
}
button{
  width: 20%;
  border: 0;
  padding: 9px 0;
  margin: 0;
  outline: 0;
  color:#FFF;
  font-weight: bold;
  cursor: pointer;
  background: #4dc55b;
}
ul{
  list-style: none;
padding: 0;
}
li{
  margin: 15px 0;
}
li span{
  display: inline-block;
  width: 60%;
  color:#6a6a6a;

}
li > button:first-of-type {
  background:#6a6a6a ;
  color:#FFF;
  margin-right: 1%;
width: 19%;
}
li > button:last-of-type{
  background: #d36060;
  color: #FFF;
}
ul > form{
margin: 15px 0;
}